.mapContainer {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  overflow: hidden;
}

.sidebarStyle {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  margin: 12px;
  background-color: #404040;
  color: #ffffff;
  z-index: 1 !important;
  padding: 6px;
  font-weight: bold;
}

.searchStyle {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  margin: 6px;
  margin-left: 4px;
  color: #ffffff;
  z-index: 1 !important;
  padding: 12px;
}

.searchStyleIner {
  display: inline-block;
  padding: 5px;
}
